.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #f0f2f5;
  color: #333;
  padding: 20px;
}

h1 {
  color: #4a90e2;
}

.slider-container {
  display: flex;
  justify-content: center;
  gap: 2px;
}

.slider {
  width: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  /* padding: 15px; */
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.slider input[type="range"]
/* .form-range */ {
  transform: rotate(90deg);

  width: 200px;
  height: 200px;
  margin: 10px 0;
  direction: rtl;
  /* writing-mode: vertical-lr; */
}

.slider label {
  /* margin-top: 10px; */
  font-size: 0.5em;
  color: #666;
}

.slider .value {
  font-size: 0.4em;
  color: #4a90e2;
  margin-bottom: 10px;
}
